@import url(https://fonts.googleapis.com/css2?family=Yrsa:wght@300&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#f7f2f7}.App>*{width:100%}

.App{max-width:1200px;background:white;display:flex;flex-wrap:wrap;margin:auto;overflow:hidden}.App p{line-height:1.3em}.App h2,.App h3,.App h1,.App h4,.App h5{font-family:'Yrsa', serif;font-size:1.4em}.Logo{color:white;font-family:'Yrsa', serif;font-size:2em;border-left:2px dashed white;padding:.4em}.SectionHeading{text-align:center}.SectionHeading h2,.SectionHeading h3,.SectionHeading h1,.SectionHeading h4,.SectionHeading h5{font-family:'Yrsa', serif;font-size:2.5em;padding-top:100px;padding-bottom:15px;margin-bottom:0px}.SectionHeading p{padding-top:0px}.Backdrop{z-index:0;position:absolute;width:100%;height:600px}.Backdrop img:nth-child(1){max-width:75px;-webkit-filter:blur(2px);filter:blur(2px);position:absolute;right:5px;top:10px}.Backdrop img:nth-child(2){max-width:45px;-webkit-filter:blur(5px);filter:blur(5px);position:absolute;right:150px;top:40px;opacity:.9}.Backdrop img:nth-child(3){max-width:25px;-webkit-filter:blur(6px);filter:blur(6px);position:absolute;left:45px;top:30px}.Backdrop img:nth-child(4){max-width:15px;-webkit-filter:blur(3px);filter:blur(3px);position:absolute;right:80px;top:120px;opacity:.7}.Backdrop img:nth-child(5){max-width:65px;-webkit-filter:blur(5px);filter:blur(5px);position:absolute;left:225px;bottom:50px;opacity:.7}.Backdrop img:nth-child(6){max-width:25px;-webkit-filter:blur(2px);filter:blur(2px);position:absolute;right:325px;top:80px}.Block{z-index:1}.Contact{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-evenly;width:100%}.Contact .Info{width:30%;min-width:250px}.Contact .Info ul{list-style:none;padding:0}.Contact .Info ul li{padding-bottom:12px}.Contact .Info ul li svg{margin-right:5px;margin-bottom:-7px}.Contact .Form{width:65%}.Contact .Form form{max-width:600px;align-self:center;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between;margin:1.5rem auto}.Contact .Form form div{display:flex;flex-direction:column;width:auto;margin:.6rem 0}.Contact .Form form div label{padding:0;margin:0}.Contact .Form form div input,.Contact .Form form div textarea{font-size:1.01em;padding:.6rem 1.2rem;border:1px solid #ccc;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;outline:none}.Contact .Form form div textarea{min-height:150px}.Contact .Form form button{border:1px solid white;padding:.6rem 1.2rem;background:none;width:5rem;padding:1rem;align-self:flex-end}.Contact .Form form button:hover{background:white;color:white;cursor:pointer}

.Slider{display:flex;flex-wrap:wrap;position:relative}.Slider .Menu{position:absolute;display:flex;justify-content:space-between;width:100%}.Slider .Menu a{text-decoration:none}.Slider .Menu div>ul{margin-top:53px;margin-right:53px}.Slider .Menu ul li{display:inline-block;padding:0 1rem}.Slider .Menu ul li a{color:white;border-bottom:0;text-decoration:none}.Slider .Menu ul li a:hover{border-bottom:2px dashed white;color:#222}.Slider .Slide{flex:1 1 100%;height:450px;width:100%}.Slider .Slide .Content{margin-top:15%;margin-left:4%;width:40%}.Slider .Slide .Content h2{font-family:'Yrsa', serif;font-size:2.2em;margin:0;padding:0}.Slider .Slide .Content p{padding:0;margin:0;line-height:1.4em}.Slider .Slide .Content button{margin-top:2em;color:white;background:none;border:1px solid white;border-radius:15px;padding:.6rem 2rem}

.Blocks{display:flex;flex-direction:row;justify-content:space-evenly;align-content:space-evenly;flex-wrap:wrap;width:100%}.Blocks .Block{text-align:center;padding-top:4rem;padding-bottom:4rem;padding-left:1.5rem;padding-right:1.5rem}.Blocks .Block h3{font-size:1.8em;font-family:'Yrsa', serif;margin-bottom:5px;padding-bottom:5px}.Blocks .Block p{padding:0px;margin:0px;width:100%}

.Footer{margin-top:2rem;background-color:#c13256;min-height:200px;width:100%;display:flex;flex-direction:row;justify-content:space-between;flex-wrap:wrap;align-content:center;align-items:center}.Footer div{height:150px;min-width:250px;color:white}.Footer div a{color:white;border-bottom:1px dotted white}.Footer div:last-child{padding-right:5rem}.Footer div h5{padding-bottom:.1rem;margin-bottom:.1rem}.Footer div ul{padding:0 0.1rem}.Footer div ul li{list-style:none;padding:0.1rem 0;color:white}.Footer div ul li a{text-decoration:none;border:none}.Footer div ul li a:hover{border-bottom:1px dashed white}.Footer div ul li svg{margin-top:4px;margin-right:5px;margin-bottom:-7px}@media only screen and (max-width: 600px){.Footer{justify-content:center}.Footer div{text-align:center;width:100%;padding:0.1rem 0;margin:0.1rem 0}}

